import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';
import CheckBox from 'components/Friend/CheckBox';
import { ReactComponent as CloseBtn } from 'images/friend/bottomsheet/close_btn.svg';
import CopyBtn from 'components/Friend/CopyBtn';
import Modal from 'components/Friend/Modal';
import { ReactComponent as Namu } from 'images/friend/account/icon_namu.svg';
import { ReactComponent as Kb } from 'images/friend/account/icon_kb.svg';
import { ReactComponent as Sk } from 'images/friend/account/icon_sk.svg';
import { ReactComponent as Shinhan } from 'images/friend/account/icon_shinhan.svg';
import KakaoBtn from 'components/Kakao/KakaoBtn';
import { useForm } from 'react-hook-form';
import Radio from 'components/Friend/Radio';

const BottomSheet = ({ onClose, type, onClick }) => {
  const Container = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.86);
    z-index: ${(props) => props.z};
    bottom: 0;
  `;
  const Contents = styled.div`
    background: #ffffff;
    width: 100%;
    padding-bottom: 20px;
    height: ${(props) => props.height || 'auto'};
    bottom: 0;
    border-radius: 15px 15px 0px 0px;
    position: absolute;
    animation: fadeInUp 1s;

    @keyframes fadeInUp {
      0% {
        opacity: 0.8;
        transform: translate3d(0, 100%, 0);
      }
      to {
        opacity: 1;
        transform: translateZ(0);
      }
    }
  `;
  const CloseBox = styled.div`
    display: flex;
    justify-content: end;
    padding: 26px 22px 0 0;
    cursor: pointer;
  `;
  const [copyModal, setCopyModal] = useState(false);

  return (
    <>
      {type === 'personalAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()} height="75vh">
              <CloseBox>
                <CloseBtn width="6.4vw" onClick={() => onClose(false)} />
              </CloseBox>
              <PersonalAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'marketingAgree' && (
        <>
          <Container z={20} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()} height="75vh">
              <CloseBox>
                <CloseBtn width="6.4vw" onClick={() => onClose(false)} />
              </CloseBox>
              <MarketingAgree />
            </Contents>
          </Container>
        </>
      )}
      {type === 'share' && (
        <>
          {copyModal && (
            <Modal type="copy" onClose={() => setCopyModal(false)} />
          )}
          <Container z={30} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <ShareDetail setCopyModal={setCopyModal} />
            </Contents>
          </Container>
        </>
      )}
      {type === 'agree' && (
        <>
          <Container z={10} onClick={() => onClose(false)}>
            <Contents onClick={(e) => e.stopPropagation()}>
              <AgreeDetail onClick={onClick} />
            </Contents>
          </Container>
        </>
      )}
    </>
  );
};

export default BottomSheet;

const AgreeDetail = ({ onClick }) => {
  const [agreeItems, setAgreeItems] = useState([
    { id: 0, name: '[] ()  ' },
    {
      id: 1,
      name: '[]      ',
    },
  ]);

  const H3 = styled.h3`
    font-size: 20px;
    line-height: 25px;
    margin: 30px 0;
  `;
  const [readMe, setReadMe] = useState(null);
  return (
    <>
      {readMe === 0 ? (
        <BottomSheet type="personalAgree" onClose={() => setReadMe(null)} />
      ) : null}
      {readMe === 1 ? (
        <BottomSheet type="marketingAgree" onClose={() => setReadMe(null)} />
      ) : null}

      <H3>
            
        <br />   .
      </H3>
      <CheckBox
        onClick={onClick}
        agreeItems={agreeItems}
        setAgreeItems={setAgreeItems}
        setReadMe={setReadMe}
      />
    </>
  );
};

const ShareDetail = ({ setCopyModal }) => {
  const [haveAccount, setHaveAccount] = useState(null);
  const [accountList, setAccountList] = useState([
    { id: 0, name: '', status: 0, checked: true },
    {
      id: 1,
      name: '',
      status: 0,
    },
    {
      id: 2,
      name: 'KB',
      status: 1,
    },
    {
      id: 3,
      name: 'SK',
      status: 2,
    },
  ]);

  //    (defaultValues) accountList  checked true .
  const checkedAccount = accountList.filter((item) => item.checked === true);
  const { register, watch } = useForm({
    mode: 'onChange',
    defaultValues: { account: checkedAccount[0].name },
  });

  console.log('  ', watch('account'));

  useEffect(() => {
    const haveAccountArray = accountList.filter((item) => item.status === 0);
    setHaveAccount(haveAccountArray.length > 0);
    console.log('   : ', haveAccountArray.length);
  }, [accountList]);

  console.log('   boolean : ', haveAccount);
  const BtnBox = styled.div`
    display: flex;
    gap: 8px;
    margin: 8px;
  `;
  const Title = styled.div`
    display: flex;
    gap: 16px;
    align-items: center;
    p {
      margin: 0;
      font-size: 16px;
      font-weight: 700;
    }
  `;
  const AccountBox = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 15px;
    align-items: center;
    cursor: pointer;
    label {
      cursor: pointer;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  `;
  const AccountContainer = styled.div`
    margin: 45px 0;
  `;
  const Container = styled.div`
    h3 {
      margin: 30px 0 0 0;
      font-size: 20px;
      line-height: 150%;
    }
    h4 {
      margin: 10px 0 0 0;
      font-size: 14px;
      color: #757575;
      font-weight: 400;
    }
  `;

  const CreateBtn = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    height: 32px;
    font-size: 14px;
    font-weight: 700;
    padding: 8px 12px 8px 12px;
    color: #ffffff;
    text-align: center;
    line-height: 100%;
    border-radius: 8px;
    background-color: #fe5000;
    border: none;
    &:hover {
      background-color: #cc4400;
    }
  `;

  return (
    <Container>
      <div>
        {haveAccount ? (
          <h3>
             
            <br />  
          </h3>
        ) : (
          <h3>
              <br />
              
          </h3>
        )}

        <h4>    </h4>
        <AccountContainer>
          {accountList.map((account) => (
            <div key={account.id}>
              {account.status === 0 && (
                <AccountBox key={account.id}>
                  <Radio
                    account={account}
                    register={register('account', { required: true })}
                    watch={watch('account')}
                    name="account"
                    value={account.name}
                    id={account.id}
                  />
                </AccountBox>
              )}

              {account.status === 1 && (
                <AccountBox key={account.id}>
                  <Title>
                    {account.id === 0 && (
                      <div>
                        <Namu width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 1 && (
                      <div>
                        <Shinhan width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 2 && (
                      <div>
                        <Kb width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 3 && (
                      <div>
                        <Sk width="38px" height="38px" />
                      </div>
                    )}
                    <p>{account.name}</p>
                  </Title>
                  <CreateBtn
                    onClick={() => {
                      console.log('   :', account.name);
                      console.log('  id :', account.id);
                    }}
                  >
                    <div></div>
                  </CreateBtn>
                </AccountBox>
              )}
              {account.status === 2 && (
                <AccountBox key={account.id}>
                  <Title>
                    {account.id === 0 && (
                      <div>
                        <Namu width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 1 && (
                      <div>
                        <Shinhan width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 2 && (
                      <div>
                        <Kb width="38px" height="38px" />
                      </div>
                    )}
                    {account.id === 3 && (
                      <div>
                        <Sk width="38px" height="38px" />
                      </div>
                    )}
                    <p>{account.name}</p>
                  </Title>
                  <CreateBtn
                    onClick={() => {
                      console.log('   :', account.name);
                      console.log('  id :', account.id);
                    }}
                  >
                    <div>  </div>
                  </CreateBtn>
                </AccountBox>
              )}
            </div>
          ))}
        </AccountContainer>
        {haveAccount ? (
          <BtnBox>
            <CopyBtn active={true} setCopyModal={setCopyModal} />
            <KakaoBtn active={true} />
          </BtnBox>
        ) : (
          <BtnBox>
            <CopyBtn active={false} setCopyModal={setCopyModal} />
            <KakaoBtn active={false} />
          </BtnBox>
        )}
      </div>
    </Container>
  );
};

